import React, { useState, useContext } from 'react';
import { useHistory } from "react-router-dom";

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { ImageContext } from './context/image-context';
import Cropper from './Cropper';
import CropDialog from './CropDialog';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
}));

const Crop = () => {
    const classes = useStyles();
    let history = useHistory();

    // eslint-disable-next-line
    const { imgSrc, setImgSrc } = useContext(ImageContext);

    // eslint-disable-next-line
    const { hasCropped, setHasCropped } = useContext(ImageContext);

    const [result, setResult] = useState();


    const goBackHandler = () => {
        history.goBack();
    }

    const cropHandler = () => {
        if (result) {
            setImgSrc(result);
        }
        setHasCropped(true);
        history.push("/upload");
    }
    const [open, setOpen] = useState(true);

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <React.Fragment>
            <CropDialog open={open} handleClose={handleClose} />
            <div className={classes.root} style={{ backgroundColor: "black", height: "100%", overflow: "auto" }}>
                <AppBar position="fixed" style={{ backgroundColor: "#303030", height: "10%" }}>
                    <Toolbar>
                        <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" onClick={goBackHandler}>
                            <ArrowBackIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                        </Typography>
                        <Button color="inherit" onClick={cropHandler}>CROP</Button>
                    </Toolbar>
                </AppBar>
                <div style={{ height: "80%", width: "95%", position: "fixed", top: "12%", left: "2.5%" }}>
                    <Cropper className="react-crop-parent" result={result} setResult={setResult} />
                </div>
            </div>
        </React.Fragment>
    )
}

export default Crop;

