import React, { useState } from 'react';
import {
  HashRouter as Router,
  Route,
  Redirect,
  Switch
} from 'react-router-dom';

import { ImageContext } from './components/context/image-context';
import Landing from './components/Landing';
import Crop from './components/Crop';
import Upload from './components/Upload';



const App = () => {
  const [imgSrc, setImgSrc] = useState(null);
  const [hasCropped, setHasCropped] = useState(false);
  let routes;

  if (imgSrc && hasCropped) {
    routes = (
      <Switch>
        <Route path="/" exact>
          <Landing />
        </Route>
        <Route path="/upload" exact>
          <Upload />
        </Route>
        <Redirect to="/" />
      </Switch>
    )
  } else if (imgSrc) {
    routes = (
      <Switch>
        <Route path="/" exact>
          <Landing />
        </Route>
        <Route path="/crop" exact>
          <Crop />
        </Route>
        <Route path="/upload" exact>
          <Upload />
        </Route>
        <Redirect to="/" />
      </Switch>
    )
  } else {
    routes = (
      <Switch>
        <Route path="/" exact>
          <Landing />
        </Route>
        <Redirect to="/" />
      </Switch>
    )
  }

  return (
    <Router>
      <ImageContext.Provider value={{ imgSrc, setImgSrc, hasCropped, setHasCropped }}>
        {routes}
      </ImageContext.Provider>
    </Router>
  );
}

export default App;
