import React, { useState, useEffect } from 'react';
/* eslint-disable no-use-before-define */
import TextField from "@material-ui/core/TextField";
import Autocomplete from '@material-ui/lab/Autocomplete';
import CircularProgress from '@material-ui/core/CircularProgress';

async function GetDiseases(uri) {
  const response = await fetch(uri);
  let diseases_json = await response.json();
  diseases_json = diseases_json.map(disease => { return String(disease).toUpperCase(); });
  diseases_json = [...new Set(diseases_json)];
  diseases_json = diseases_json.sort();
  return diseases_json;
}

const DiseaseSelector = props => {
  const [diseases, setDiseases] = useState([]);
  const loading = diseases.length === 0;
  const uri = props.uri;

  useEffect(() => {
    let active = true;

    (async () => {
      // Fetch Diseases from URI
      // Ensures they are all in
      // Upper Case
      // And Sorted in Alphabetical Order
      const diseases_json = await GetDiseases(uri);
      if (active) {
        setDiseases(diseases_json);
      }
    })();

    return () => {
      active = false;
    };
  }, [loading, uri]);

  return (
    <Autocomplete
      id="disease-select"
      style={{ width: 300, display: "inline-block" }}
      freeSolo
      getOptionSelected={(option, value) => option === value}
      getOptionLabel={option => option}
      options={diseases}
      loading={loading}
      onInputChange={(e, v) => props.onDiseaseChange(v)}
      renderInput={params => (
        <TextField
          {...params}
          label="Select Disease"
          fullWidth
          variant="outlined"
          InputProps={{
            ...params.InputProps,
            endAdornment: (
              <React.Fragment>
                {loading ? <CircularProgress color="inherit" size={20} /> : null}
                {params.InputProps.endAdornment}
              </React.Fragment>
            ),
          }}
        />
      )}
    />
  );
}

export default DiseaseSelector;