import React, { useState, useContext } from "react";
import { useHistory } from "react-router-dom";
import { ImageContext } from "./context/image-context";
import DiseaseSelector from "./DiseaseSelector";
import UploadDialog from "./UploadDialog";
import SuccessDialog from "./SuccessDialog";
import Cancel from "./Cancel";
import Container from "@material-ui/core/Container";
import Button from "@material-ui/core/Button";
import PublishIcon from "@material-ui/icons/Publish";
import firebase from "firebase/app";
import 'firebase/storage';
import 'firebase/auth';

function Base64MimeType(encoded) {
  if (typeof encoded !== "string") {
    return null;
  }
  var mime = encoded.match(/data:([a-zA-Z0-9]+\/[a-zA-Z0-9-.+]+).*,.*/);
  if (mime && mime.length) {
    return mime[1];
  }
  return null;
}

function GetCurrentAppUser() {
  const user = firebase.auth().currentUser;
  if (user == null)
    return "user";
  const email = user.email;
  if (email == null || email === "")
    return "user";
  return email;
}

const Upload = () => {
  let history = useHistory();

  // eslint-disable-next-line
  const { imgSrc, setImgSrc, hasCropped, setHasCropped } = useContext(ImageContext);

  // eslint-disable-next-line
  const [disease, setDisease] = useState(null);
  const [open, setOpen] = useState(false);
  const [success, setSuccess] = useState(false);

  const handleClose = () => {
    if (open)
      setOpen(false);
  };

  const handleOpen = () => {
    if (!open)
      setOpen(true);
  };

  const successClose = () => {
    setSuccess(false);
    setHasCropped(false);
    history.push("/");
  };

  const successOpen = () => {
    setSuccess(true);
  };

  async function UploadHandler() {
    if (imgSrc == null || disease == null || disease.trim() === "") return; //TODO: Error
    try {
      handleOpen();

      const disease_selected = String(disease).trim().toUpperCase();
      const upload_dir = "images/" + disease_selected + "/";

      const user = GetCurrentAppUser();
      const file_name = Date.now() + "_" + user;

      const contentType = Base64MimeType(imgSrc);

      const upload_task = await firebase
        .storage()
        .ref(upload_dir)
        .child(file_name)
        .putString(imgSrc, "data_url", { contentType: contentType });

      handleClose();
      if (upload_task.state === "success")
        successOpen();
      else {
        throw new Error("Failed " + upload_task.state);
      }
    } catch (err) {
      handleClose();
      console.error(err);
    }
  }

  return (
    <React.Fragment>
      <UploadDialog open={open} />
      <SuccessDialog isOpen={success} successClose={successClose} />
      <Container style={{ marginTop: "20%", textAlign: "center" }}>
        <img
          src={imgSrc}
          alt="Crop"
          style={{ width: "50%", height: "50%" }}
        />
        <div className="center" style={{ color: "#808080", fontSize: "20px" }}>
          <p>Write or select disease name.</p>
        </div>
        <div style={{ textAlign: "center" }}>
          <DiseaseSelector uri="/diseases.json" onDiseaseChange={setDisease} />
        </div>
        <Button
          onClick={UploadHandler}
          variant="contained"
          color="primary"
          component="span"
          style={{ marginTop: "5%" }}
        >
          <PublishIcon /> &nbsp; UPLOAD IMAGE
        </Button>
        <div>
          <Cancel />
        </div>
      </Container>
    </React.Fragment>
  );
};

export default Upload;
