import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';
import BeenhereIcon from '@material-ui/icons/Beenhere';

const SuccessDialog = props => {
    const { isOpen, successClose } = props;
    
    return (
        <Dialog 
            disableBackdropClick={true}
            disableEscapeKeyDown={true}
            open={isOpen}>
            <div style={{ 
                    paddingTop: "50px",
                    paddingBottom: "50px",
                    paddingLeft: "20px",
                    paddingRight: "20px",
                    textAlign: "center",
                    fontSize: "100px" 
                }}>
                <BeenhereIcon 
                    color='primary'
                    fontSize='inherit'
                />
                <h5 style={{ 
                    margin: "0px",
                    paddingTop: "4px",
                    paddingLeft: "20px",
                    fontSize: "20px",
                    fontWeight: "500" 
                }}>Image Uploaded Successfully</h5>
            </div>
            <DialogActions>
                <Button onClick={successClose} style={{ fontSize: "15px" }} color="primary" autoFocus>
                    OK
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default SuccessDialog;