import React, { useEffect, useContext } from 'react';
import { useHistory } from "react-router-dom";

import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import { ImageContext } from './context/image-context';

const useStyles = makeStyles(theme => ({
    root: {
      '& > *': {
        margin: theme.spacing(1),
      },
    },
    input: {
      display: 'none',
    },
}));

const Landing = () => {
    const history = useHistory();

    // eslint-disable-next-line
    const {imgSrc, setImgSrc, hasCropped, setHasCropped} = useContext(ImageContext);
    const classes = useStyles();

    useEffect(() => {
        const Entry = () => {
            setImgSrc(null);
            setHasCropped(false);
        }
        Entry();
    }, [setImgSrc, setHasCropped]);

    useEffect(() => {
        if (imgSrc) {
            history.push("/crop");;
        }
    }, [imgSrc, history]);
    
    const ImageSetter = (result) => {
        setImgSrc(result);
    }

    const onSelectFile = e => {
        if (e.target.files && e.target.files.length > 0) {
            const reader = new FileReader();
            ImageSetter(null);
            reader.addEventListener("load", () => {
                ImageSetter(reader.result);
            });
            reader.readAsDataURL(e.target.files[0]);
        }
    };

    return (
        <React.Fragment>
            <div className="center" style={{ textAlign: "center", paddingTop: "100px" }}>
                <div className="center" style={{ textAlign: "center", padding: "30px", color: "#808080", fontSize: "20px" }}>
                    <p>This app is meant to collect images for clinical trial purposes only.</p>
                    <p>Click on button below, crop affected part, choose disease or name a new disease and upload</p>
                </div>
                <div style={{ marginTop: "30px", marginBottom: "30px"}}>
                    <input
                        accept="image/*"
                        className={classes.input}
                        id="outlined-button-camera"
                        type="file"
                        onChange={onSelectFile}
                        capture
                    />
                    <label htmlFor="outlined-button-camera">
                        <Button variant="contained" color="primary" component="span">
                            <PhotoCamera /> &nbsp; CAMERA
                        </Button>
                    </label>
                </div>
                <div style={{ marginTop: "50px", marginBottom: "30px" }}>
                    <input
                        accept="image/*"
                        className={classes.input}
                        id="outlined-button-file"
                        type="file"
                        onChange={onSelectFile}
                    />
                    <label htmlFor="outlined-button-file">
                        <Button variant="contained" color="primary" component="span">
                            <FileCopyIcon /> &nbsp; CHOOSE IMAGE
                        </Button>
                    </label>
                </div>
            </div>
        </React.Fragment>
    )
};

export default Landing;
